.matchcoder {
    border-radius: 0.5em;
    width: 60em;
    border: 1px solid grey;
    background-color: white;
    padding: 0px;
    text-align: center;
    display: block;
    outline: none;
}

.matchcoder::part(content) {
    padding: 0px;
    display: flex;
    height: 40em;
    flex-direction: column;
}



.default {
    padding-left: 10px;
    padding-right: 10px;
    border: 1px solid transparent;
    padding: 4px;
}

.selected {
    padding-left: 10px;
    padding-right: 10px;
    border: 1px solid lightgreen;
    padding: 4px;
}

